@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Exo&display=swap);
@import url(https://fonts.googleapis.com/css?family=Exo&display=swap);
:root {
    --main-bg-color: rgba(33, 33, 33, 1);
    --light-bg-color: rgba(72, 72, 72, 1);
    --light-bgNav-color: rgba(72, 72, 72, .8);
    --dark-bg-color: rgba(0, 0, 0, 1);
    --main-blue-color: rgba(0, 229, 255, 1);
    --light-blue-color: rgba(110, 255, 255, 1);
    --dark-blue-color: rgba(0, 178, 204, 1);
    --accent-color: #ffff6b;
}

body {
    overflow-x: hidden;
    scroll-behavior: smooth;
}

body {
    margin: 0;
    background-image:
        -webkit-gradient(linear,
            left top, left bottom,
            from(rgba(33, 33, 33, .85)),
            to(rgba(33, 33, 33, .85))),
        url(/portfolio/static/media/city-ConvertImage.515f2c5d.jpg);
    background-image:
        -webkit-linear-gradient(rgba(33, 33, 33, .85),
            rgba(33, 33, 33, .85)),
        url(/portfolio/static/media/city-ConvertImage.515f2c5d.jpg);
    background-image:
        linear-gradient(rgba(33, 33, 33, .85),
            rgba(33, 33, 33, .85)),
        url(/portfolio/static/media/city-ConvertImage.515f2c5d.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    font-family: 'Open Sans', sans-serif;
}

.home {
    height: 100vh;
    width: 100vw;
    text-align: center;
    overflow-y: hidden;
    overflow-x: hidden
}

.header {
    grid-column: 2/4;
    grid-row: 1;
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-top: 4em;
}

.main-header {
    grid-column: 1/4;
    font-size: 7em;
    font-weight: bold;
    color: #FFFFFF;
    margin-bottom: 0;
    margin-top: 0;
    justify-self: center;
    align-content: start;
    -webkit-margin-before: 0;
            margin-block-start: 0;
    border-bottom: 1px solid var(--light-bg-color);
}


.last-name {
    color: var(--main-blue-color);
}

.sub-header {
    grid-column: 1/3;
    margin-bottom: 4em;
    margin-top: .5em;
    align-self: end;
    font-size: 1.5em;
    color: var(--dark-blue-color);
    justify-self: center
}

.sub-header>span {
    color: #FFFFFF;
}

.bubbles {
    grid-column: 1/5;
    grid-row: 4;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    justify-content: space-between;
    justify-items: center;
    padding-top: 2em;
}

.nav-bubble {
    grid-template-areas: inherit;
    width: 11em;
    height: 11em;
    border-radius: 50%;
    border: none;
    text-align: center;
    background: -webkit-gradient(linear, left top, left bottom, from(var(--light-bg-color)), to(var(--dark-bg-color)));
    background: -webkit-linear-gradient(var(--light-bg-color), var(--dark-bg-color));
    background: linear-gradient(var(--light-bg-color), var(--dark-bg-color));
    outline: none;
    font-size: 2em;
    color: #FFFFFF;
    text-decoration: none;
    text-shadow: 2px 2px 2px var(--light-bg-color);
}

.nav-bubble:hover {
    -webkit-transition: .5s border ease-in-out;
    transition: .5s border ease-in-out;
    border: 2px dashed var(--light-blue-color);
    cursor: pointer;
}


a:link {
    text-decoration: none
}


@media only screen and (max-width : 1500px) {
    .main-header {
        font-size: 6em;
    }

    .nav-bubble {
        grid-template-areas: inherit;
        width: 10em;
        height: 10em;
        border-radius: 50%;
        border: none;
        text-align: center;
        background: -webkit-gradient(linear, left top, left bottom, from(var(--light-bg-color)), to(var(--dark-bg-color)));
        background: -webkit-linear-gradient(var(--light-bg-color), var(--dark-bg-color));
        background: linear-gradient(var(--light-bg-color), var(--dark-bg-color));
        outline: none;
        font-size: 1.5em
    }

    .bubbles {
        padding-top: 1em
    }

}

@media only screen and (max-width: 992px) {
    .main-header {
        font-size: 4em
    }

    .nav-bubble {
        width: 8em;
        height: 8em
    }

}


@media only screen and (max-width : 576px) {

    .main-header {
        font-size: 3.5em
    }

    .header {
        margin-top: 1.2em
    }

    .bubbles {
        display: flex;
        flex-direction: column;
        justify-self: center;
        padding-top: 0;
        grid-row: 2;
        grid-column: 1/4;
        align-items: center;
    }

    .nav-bubble {
        width: 9em;
        height: 9em;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        font-size: 1.2em;
        margin-bottom: 1em;
        border: 1px solid var(--main-blue-color)
    }


    .sub-header {
        margin-bottom: 2em;
        font-size: 1.1em
    }
}
.nav-menu {
    background: var(--light-bg-color);
    width: 20em;
    height: calc(100vh - 5em);
    padding: 1em;
    z-index: 200;
    position: fixed;
    margin-right: 0;
    margin-top: 3em;
    padding-top: 1em;
    padding-bottom: 1em;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    box-shadow: 1px 1px 2px var(--main-bg-color),
        3px 3px 5px var(--dark-blue-color),
        3px 3px 10px var(--main-blue-color)
}


.nav-menu-bubble {
    display: grid;
    grid-template-areas: inherit;
    width: 20em;
    height: 20em;
    border-radius: 50%;
    border: none;
    text-align: center;
    background: -webkit-gradient(linear, left top, left bottom, from(var(--light-bg-color)), to(var(--dark-bg-color)));
    background: -webkit-linear-gradient(var(--light-bg-color), var(--dark-bg-color));
    background: linear-gradient(var(--light-bg-color), var(--dark-bg-color));
    z-index: 1;
    outline: none;
}

.nav-menu-bubble:hover {
    -webkit-transition: .5s border ease-in-out;
    transition: .5s border ease-in-out;
    border: 2px dashed var(--light-blue-color);
    cursor: pointer;
}

.nav-menu-bubble>h6 {
    align-self: center;
    font-size: 1.7em;
    color: #FFFFFF;
    text-decoration: none;
    text-shadow: 2px 2px 2px var(--light-bg-color);
}

a:link {
    text-decoration: none
}

@media only screen and (max-width : 1920px) {
    .nav-menu {
        justify-content: space-between;
        width: 16em
    }

    .nav-menu-bubble {
        height: 16em;
        width: 16em
    }
}


@media only screen and (max-width : 576px) {

    .nav-menu {
        width: 60vw;
        justify-content: space-around;
        height: calc(100vh - 12em)
    }

    .nav-menu-bubble {
        border: 2px solid var(--light-blue-color);
        cursor: pointer;
    }

}
.nav {
    top: 0;
    left: 0;
    background-color: var(--dark-bg-color);
    display: grid;
    height: 3em;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: auto;
}

.navbar {
    grid-column: 1/4;
    grid-row: 1;
    max-height: 3em;
    align-items: center;
}

.menu-btn {
    border-radius: 20%;
    margin-left: 3em;
    margin-top: .75em;
    align-self: center
}

.menu-btn:hover {
    cursor: pointer
}

svg.menu-btn {
    height: 1.5em;
    width: 1.5em;
}

div.logo {
    grid-column: 4;
    justify-self: center;
    align-self: center;
    font-size: 1.3em;
    color: var(--main-blue-color);
    justify-self: end;
    align-self: center;
    margin-right: 3em;
    font-weight: bold;
    font-family: 'Exo', sans-serif;
}

@media only screen and (max-width : 960px) {
    div.logo {
        grid-column: 3/5;
        grid-row: 1;
        display: none
    }
}
.backdrop {
    height: calc(100vh - 3em);
    width: 100vw;
    z-index: 100;
    top: 3em;
    position: absolute;
    background-color: rgba(0, 0, 0, .7);
}
nav.projectsNav {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    position: fixed;
    top: 0;
    left: 0;
    height: 2.5em;
    width: 100%;
    background-color: var(--light-bgNav-color);
    color: #ffffff;
    -webkit-transition: .25s;
    transition: .25s;
    margin-top: 0;
    border-bottom: 1px solid var(--dark-bg-color);
    z-index: 5
}

.projectsBtn {
    height: 100%;
    width: 10em;
    background: transparent;
    color: var(--main-blue-color);
    outline: none;
    font-size: 100%;
    border-left: 1px solid var(--dark-bg-color)
}

.projectsBtn:hover {
    cursor: pointer;
}

.topScrollBtn {
    background-color: #ffffff;
    color: var(--dark-bg-color);
    border-left: 1px solid var(--dark-bg-color)
}

@media only screen and (max-width : 960px) {
    .projectsBtn {
        width: 8em;
        font-size: 12px
    }
