.about-me {
    height: 100%;
    width: 100vw;
    background-image:
        -webkit-gradient(linear,
            left top, left bottom,
            from(rgba(33, 33, 33, .85)),
            to(rgba(33, 33, 33, .85))),
        url(/portfolio/static/media/city-ConvertImage.515f2c5d.jpg);
    background-image:
        -webkit-linear-gradient(rgba(33, 33, 33, .85),
            rgba(33, 33, 33, .85)),
        url(/portfolio/static/media/city-ConvertImage.515f2c5d.jpg);
    background-image:
        linear-gradient(rgba(33, 33, 33, .85),
            rgba(33, 33, 33, .85)),
        url(/portfolio/static/media/city-ConvertImage.515f2c5d.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.about-me-container {
    display: flex;
    flex-direction: column;
}


.intro>h4 {
    text-align: center;
    color: #FFFFFF
}

.h1-container {
    justify-self: center
}

.h1-container>h1 {
    background-image: -webkit-gradient(linear, 0% 0%, 75% 100%, from(#FFFFFF), to(var(--accent-color)));
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 4em;
    border-bottom: 2px solid var(--main-blue-color);
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content
}

.h1-container.about-header {
    display: flex;
    flex-direction: row;
    justify-content: center
}

div.about-me-container>.section-bg {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}

.section-bg {
    line-height: 3em;
    color: #FFFFFF;
    background-color: var(--light-bg-color);
    border: 1px solid var(--dark-bg-color);
    border-radius: 5%;
    padding: 1.5em;
    padding-bottom: 3em;
    height: 100vh;
    padding-right: 1em;
    margin-bottom: 25em;
}

.p-text {
    padding-right: 1em
}

.linksBtn {
    display: flex;
    justify-content: space-around;
}

.linksBtn>a {
    color: var(--main-blue-color);
    font-size: 1.2em;
    text-decoration: underline
}

.linksBtn>a:hover {
    color: var(--light-blue-color);
    cursor: pointer
}

.arrow {
    border: solid var(--accent-color);
    border-width: 0 2px 2px 0;
    display: inline-block;
    padding: 3px;
    margin-left: 3px;
    transform: rotate(-135deg);
    -webkit-transform: rotate(-135deg);
}

.intro-span {
    font-size: 2em;
    color: var(--main-blue-color);
    font-family: 'Exo', sans-serif;
}

img {
